input[type="radio"]{
    display: none;
}
input[type="radio"]+label {
    display : inline-block;
    margin-left : -2em;
    line-height : 1.5em;
    
}
input[type=radio]:not(old)+label>span {
    display: inline-block;
    width: 0.875em;
    height: 0.875em;
    margin: 0.25em 0.5em 0.25em 0.25em;
    border: 0.0625em solid rgb(192, 192, 192);
    border-radius: 0.25em;
    background: rgb(224, 224, 224);
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(240, 240, 240)), to(rgb(224, 224, 224)));
    background-image: linear-gradient(rgb(240, 240, 240), rgb(224, 224, 224));
    vertical-align: bottom;
}
input[type=radio]:not(old):checked+label>span {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(224, 224, 224)), to(rgb(240, 240, 240)));
    background-image: linear-gradient(rgb(224, 224, 224), rgb(240, 240, 240));
}
input[type=radio]:not(old):checked+label>span>span {
    display: block;
    width: 0.5em;
    height: 0.5em;
    margin: 0.125em;
    border: 0.0625em solid rgb(115, 153, 77);
    border-radius: 0.125em;
    background: rgb(153, 204, 102);
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(179, 217, 140)), to(rgb(153, 204, 102)));
    background-image: linear-gradient(rgb(179, 217, 140), rgb(153, 204, 102));
}


